/* GALLERY */
.gallery {
  background: linear-gradient(to bottom, #fff 65%, var(--dark-gray) 35%);
}

/* GALLERY MEDIA */
@media screen and (min-width: 576px) {
}

@media screen and (min-width: 992px) {
  .gallery {
    position: relative;
    margin-top: 70px;
  }

  .gallery::before {
    position: absolute;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    content: '';
    background: rgba(1, 1, 1, 0.7);
  }

  .no-webp .gallery,
  .no-js .gallery {
    background: center / cover url(../../img/gallery-bg.jpg) no-repeat;
  }

  .webp .gallery {
    background: center / cover url(../../img/gallery-bg.webp) no-repeat;
  }
}

@media screen and (min-width: 1200px) {
  .gallery {
    margin-top: 150px;
  }
}
